import { create } from 'zustand';

export type StoreGeneralInfoInput = {
  companyName: string;
  brandName: string;
  logo?: string;
  taxCode: string;
  registrationNumber: string;
  representativeName: string;
  representativeTitle: string;
  hotline: string;
  email: string;
  website: string;
  headquartersAddress: string;
  ward: string;
  district: string;
  province: string;
  note: string;
  bankAccountName: string;
  bankAccountNumber: string;
  bankName: string;
};

export type StoreGeneralInfo = StoreGeneralInfoInput & {
  updatedAt?: string | undefined;
  updatedBySystemId?: string | undefined;
  updatedByName?: string | undefined;
};

interface StoreInfoState {
  info: StoreGeneralInfo;
  updateInfo: (
    values: StoreGeneralInfoInput,
    metadata?: { updatedBySystemId?: string | undefined; updatedByName?: string | undefined }
  ) => void;
  reset: () => void;
  loadFromAPI: () => Promise<void>;
  saveToAPI: () => Promise<void>;
}

const defaultInfo: StoreGeneralInfo = {
  companyName: '',
  brandName: '',
  logo: '',
  taxCode: '',
  registrationNumber: '',
  representativeName: '',
  representativeTitle: '',
  hotline: '',
  email: '',
  website: '',
  headquartersAddress: '',
  ward: '',
  district: '',
  province: '',
  note: '',
  bankAccountName: '',
  bankAccountNumber: '',
  bankName: '',
  updatedAt: undefined,
  updatedBySystemId: undefined,
  updatedByName: undefined,
};

export const useStoreInfoStore = create<StoreInfoState>()(
  (set) => ({
    info: { ...defaultInfo },
    updateInfo: (values, metadata) =>
      set(() => ({
        info: {
          ...values,
          updatedAt: new Date().toISOString(),
          updatedBySystemId: metadata?.updatedBySystemId,
          updatedByName: metadata?.updatedByName,
        },
      })),
    reset: () => set(() => ({ info: { ...defaultInfo } })),
    // Load from API - should be called on app init
    loadFromAPI: async () => {
      try {
        const res = await fetch('/api/settings?key=store-info');
        if (res.ok) {
          const data = await res.json();
          if (data?.value) {
            set({ info: { ...defaultInfo, ...data.value } });
          }
        }
      } catch (error) {
        console.error('[StoreInfo] Failed to load from API:', error);
      }
    },
    // Save to API
    saveToAPI: async () => {
      try {
        const state = useStoreInfoStore.getState();
        await fetch('/api/settings', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            key: 'store-info',
            group: 'store',
            value: state.info,
          }),
        });
      } catch (error) {
        console.error('[StoreInfo] Failed to save to API:', error);
      }
    },
  })
);

export function getDefaultStoreInfo(): StoreGeneralInfo {
  return { ...defaultInfo };
}
